<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name : 'cart' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
           
        </h1>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText v-model="formData.name" type="text" :error="formError.name"
            title="" placeholder="  " />
            <BaseFormText v-model="formData.address" type="text" :error="formError.address"
            title=" " placeholder="  " />
            <BaseFormText v-model="formData.phone" type="tel" :error="formError.phone"
            title="" placeholder="  " />
            <BaseFormText v-model="formData.email" type="email" :error="formError.email"
            title="Email" placeholder="  Email" />
            <BaseFormTextaria v-model="formData.comment" :error="formError.comment"
            title="  " placeholder=" " />
          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <OrderOptions :deliveryType="deliveryType"
             :TypeId.sync="deliveryTypeId" name="del" />
            <!-- <ul class="cart__options options">
              <li class="options__item" v-for="item in deliveryType" :key="item.id">
                <label class="options__label" @click="getpaymentType(item.id)">
                  <input class="options__radio sr-only" type="radio"
                  name="delivery" :value="item.id" v-model.number="deliveryTypeId"
                  :error="formError.deliveryTypeId" checked="">
                  <span class="options__value">
                    {{item.title}} <b>{{item.price}}</b>
                  </span>
                </label>
              </li>
            </ul> -->
            <h3 class="cart__title"></h3>
            <div v-if="loadPaymentType"> </div>
            <OrderOptions  v-else
            :deliveryType="paymentType"
            :TypeId.sync="paymentTypeId" name="cash"/>
            <!-- <OrderOptions v-else :deliveryType="paymentType" :TypeId.sync="paymentTypeId"/> -->
            <!-- <ul class="cart__options options" v-else>
              <li class="options__item" v-for="item in paymentType" :key="item.id">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio"
                  name="pay" :value="item.id" v-model.number="paymentTypeId" checked="">
                  <span class="options__value">
                    {{item.title}}
                  </span>
                </label>
              </li>
            </ul> -->
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.basketId">
              <h3>{{item.product.title}} <b>{{item.product.colors.color.title}}
                {{item.product.size.title}}</b></h3>
              <b>{{(item.product.price * item.amount) | numberFormat}} </b>
              <span>: {{item.product.id}}</span>
            </li>
          </ul>
          <div class="cart__total">
            <p>: <b>{{delivetItem}}</b></p>
            <p>: <b>{{totalAmount}}</b>   
            <b>{{totalPrice + delivetPrice | numberFormat}} </b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
             
          </button>
          <div class="cart__error form__error-block"
          v-if="formError.deliveryTypeId">{{formError.deliveryTypeId}}</div>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>  !</h4>
          <p>
              .      .
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters } from 'vuex';
import BaseFormTextaria from '@/components/BaseFormTextaria.vue';
import BaseFormText from '@/components/BaseFormText.vue';
import numberFormat from '@/helpers/numberFormat';
import OrderOptions from '@/components/OrderOptions.vue';
import axios from 'axios';

export default {
  components: { BaseFormText, BaseFormTextaria, OrderOptions },
  data() {
    return {
      paymentPrice: 0,
      loadPaymentType: false,
      deliveryTypeId: 0,
      paymentTypeId: 0,
      delivery: [],
      paymentType: null,
      formData: {},
      formError: {},
      formErrorMessage: '',
    };
  },
  filters: { numberFormat },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalAmount: 'totalAmount' }),
    deliveryType() {
      return this.delivery;
    },
    delivetItem() {
      try {
        return this.delivery.find((item) => item.id === this.deliveryTypeId).title;
      } catch {
        return '';
      }
    },
    delivetPrice() {
      try {
        return Number(this.delivery.find((item) => item.id === this.deliveryTypeId).price);
      } catch {
        return '';
      }
    },
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      return axios
        .post('https://vue-moire.skillbox.cc/api/orders', {
          ...this.formData,
          paymentTypeId: this.paymentTypeId,
          deliveryTypeId: this.deliveryTypeId,
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          },
        })
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        });
    },
    getDeliveries() {
      axios
        .get('https://vue-moire.skillbox.cc/api/deliveries')
        .then((response) => { this.delivery = response.data; });
    },
    getpaymentType(value) {
      this.loadPaymentType = true;
      axios
        .get('https://vue-moire.skillbox.cc/api/payments', {
          params: {
            deliveryTypeId: value,
          },
        })
        .then((response) => { this.paymentType = response.data; })
        .then(() => { this.paymentTypeId = 0; })
        .then(() => { this.loadPaymentType = false; });
    },
    // getDeliveries() {
    //   return axios
    //   .get('https://vue-moire.skillbox.cc/api/deliveries')
    //     .then((response) => this.delivery = response.data);
    // },
  },
  watch: {
    deliveryTypeId(value) {
      this.getpaymentType(value);
    },
  },
  created() {
    this.getDeliveries();
  },
};
</script>
