<template>
  <li class="cart__item product">
  <div class="product__pic">
    <img :src="photo"
    width="120" height="120" :alt="item.product.title">
  </div>
  <h3 class="product__title">
    {{item.product.title}}
  </h3>
  <p class="product__info product__info--color">
    :
    <span>
      <i :style="{background: item.product.colors.color.code}"></i>
      {{item.product.colors.color.title}}
    </span>
    <span>: {{item.product.size.title}}</span>
  </p>
  <span class="product__code">
    : {{item.product.id}}
  </span>

  <div class="product__counter form__counter">
    <BtnMines :item="item" />

    <input type="number" v-model.number="amount">

    <BtnPlus :item="item" />
  </div>

  <b class="product__price">
    {{(item.product.price * item.amount) | numberFormat}} 
  </b>

  <button class="product__del button-del" type="button" @click="deleteProduct(item.basketItemId)"
  aria-label="   ">
    <svg width="20" height="20" fill="currentColor">
      <use xlink:href="#icon-close"></use>
    </svg>
  </button>
</li>
</template>

<script>
import BtnPlus from '@/components/BtnPlus.vue';
import BtnMines from '@/components/BtnMines.vue';
import numberFormat from '@/helpers/numberFormat';

export default {
  components: { BtnPlus, BtnMines },
  props: ['item'],
  filters: { numberFormat },
  computed: {
    photo() {
      try {
        return this.item.product.colors.gallery[0].file.url;
      } catch {
        return '';
      }
    },
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { basketItemId: this.item.basketItemId, amount: value });
      },
    },
  },
  methods: {
    deleteProduct(productId) {
      this.$store.dispatch('deleteCartProduct', productId);
    },
  },
  watch: {
    amount() {
      if (this.amount < 0 || !/[1-9]/.test(this.amount)) {
        this.amount = 1;
      }
    },
  },
};
</script>
