<template>
  <main class="content container" v-if="productLoadind"> </main>
  <main class="content container" v-else-if="!productsData">  </main>
  <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link"
          :to="{ name: 'main', query: { filter: category.id } }">
            {{category.title}}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{product.title}}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570"
          :src="photo"
           alt=" ">
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">: {{product.id}}</span>
        <h2 class="item__title">
          {{product.title}}
        </h2>
        <div class="item__form">
          <div v-if="productAddSending"> </div>
          <form v-else class="form" action="#" method="POST" @submit.prevent="addToCart">
            <div class="item__row item__row--center">
              <div class="form__counter">
                <button type="button" :disabled="productAmount === 1"
                @click="removeProduct" aria-label="  ">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="number" v-model.number="productAmount" name="count">

                <button type="button" @click="addProduct" aria-label="  ">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>
              <b class="item__price">
                {{(product.price * this.productAmount) | numberFormat}} 
              </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block">
                <legend class="form__legend"></legend>
                <ul class="colors colors--black">
                  <li class="colors__item" v-for="color of product.colors" :key="color.color.id">
                    <label class="colors__label">
                      <input class="colors__radio sr-only"
                      v-model="colorId"
                      type="radio" name="color-item"
                      :value="color.color.id" checked="">
                      <span class="colors__value" :style="{background: color.color.code}">
                      </span>
                    </label>
                  </li>
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend"></legend>
                <label class="form__label form__label--small form__label--select">
                  <select class="form__select" type="text"
                  name="category" v-model="productSize">
                    <option v-for="size in product.sizes" :key="size.id" :value="size.id">
                      {{size.title}}</option>
                  </select>
                </label>
              </fieldset>
            </div>

            <button class="item__button button button--primery" type="submit">
               
            </button>
            <div v-if="productAdded">  </div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
                
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
                
            </a>
          </li>
        </ul>

        <div class="item__content">
          <h3>:</h3>

          <p>
            <span v-for="material in product.materials" :key="material.id">
              {{material.title}} <br></span>
          </p>

          <h3>:</h3>

          <p>
               . 30C  <br>
              . 110C<br>
               <br>
             <br>
              <br>
          </p>

        </div>
      </div>
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import { mapActions } from 'vuex';
import numberFormat from '@/helpers/numberFormat';

export default {
  data() {
    return {
      productSize: 0,
      productAmount: 1,
      colorId: 0,
      productsData: null,
      productLoadind: false,
      productLoadindFaled: false,
      productAdded: false,
      productAddSending: false,
    };
  },
  filters: { numberFormat },
  computed: {
    product() {
      return this.productsData;
    },
    category() {
      return this.productsData.category;
    },
    photo() {
      try {
        return this.product.colors.find((item) => item.color.id === this.colorId)
          .gallery[0].file.url;
      } catch {
        return '';
      }
    },
  },
  methods: {
    change() {
      this.productAdded = false;
    },
    addProduct() {
      this.productAmount += 1;
    },
    removeProduct() {
      this.productAmount -= 1;
    },
    ...mapActions(['addProductToCart']),
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
        colorId: this.colorId,
        sizeId: this.productSize,
      })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    loadProducts() {
      this.productLoadind = true;
      this.productLoadindFaled = false;
      axios.get(`https://vue-moire.skillbox.cc/api/products/${this.$route.params.id}`)
        .then((response) => { this.productsData = response.data; })
        .then(() => {
          this.productSize = this.productsData.sizes[0].id
            ? this.productsData.sizes[0].id : 0;
        })
        .then(() => {
          this.colorId = this.productsData.colors[0].color.id
            ? this.productsData.colors[0].color.id : 0;
        })
        .catch(() => { this.productLoadindFaled = true; })
        .then(() => { this.productLoadind = false; });
    },
  },
  watch: {
    productAdded() {
      setTimeout(this.change, 1000);
    },
    '$route.params.id': {
      handler() {
        this.loadProducts();
      },
      immediate: true,
    },
    currentColor(value) {
      this.colorId = value;
    },
    productAmount() {
      if (this.productAmount < 0 || !/[1-9]/.test(this.productAmount)) {
        this.productAmount = 1;
      }
    },
  },
};
</script>
