import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
// import { API_BASE_URL } from '../config';

Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    cartProduct: [],
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    resetCart(state) {
      state.cartProduct = [];
      state.cartProductsData = [];
    },
    deleteCartProduct(state, basketItemId) {
      state.cartProduct = state.cartProduct.filter((prod) => prod.basketItemId !== basketItemId);
    },
    updateCartProductAmount(state, { basketItemId, amount }) {
      const item = state.cartProduct.find((prod) => prod.basketItemId === basketItemId);
      if (item) {
        item.amount = amount;
      }
    },
    syncCartProducts(state) {
      state.cartProduct = state.cartProductsData.map((item) => ({
        basketItemId: item.id,
        amount: item.quantity,
        sizeId: item.size.id,
        colorId: item.color.id,
      }));
    },
    updateUserAccessKey(state, accesskey) {
      state.userAccessKey = accesskey;
    },
    addProductToItem(state, { basketItemId, amount }) {
      const item = state.cartProduct.find((prod) => prod.basketItemId === basketItemId);
      if (item) {
        item.amount = amount + 1;
      }
    },
    remuveProductToItem(state, { basketItemId, amount }) {
      const item = state.cartProduct.find((prod) => prod.basketItemId === basketItemId);
      if (item) {
        item.amount = amount - 1;
      }
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProduct.map((item) => {
        const { product } = state.cartProductsData.find((p) => p.id === item.basketItemId);
        return {
          ...item,
          product: {
            ...product,
            colors: product.colors.find((c) => c.id === item.colorId),
            size: state.cartProductsData.find((p) => p.id === item.basketItemId).size,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce(
        (acc, item) => (item.product.price * item.amount) + acc, 0,
      );
    },
    totalAmount(state, getters) {
      return getters.cartDetailProducts.reduce(
        (acc, item) => (item.amount) + acc, 0,
      );
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios.get(`https://vue-moire.skillbox.cc/api/orders/${orderId}`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
    addProductToCart(context, {
      productId, amount, colorId, sizeId,
    }) {
      return axios
        .post('https://vue-moire.skillbox.cc/api/baskets/products', {
          productId,
          colorId,
          sizeId,
          quantity: amount,

        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    loadUserAccessKey(context) {
      axios.get('https://vue-moire.skillbox.cc/api/users/accessKey')
        .then((response) => {
          localStorage.setItem('userAccessKey', response.data.accessKey);
          context.commit('updateUserAccessKey', response.data.accessKey);
        });
    },
    loadCart(context) {
      return axios.get('https://vue-moire.skillbox.cc/api/baskets', {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.accessKey);
            context.commit('updateUserAccessKey', response.data.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    updateCartProductAmount(context, { basketItemId, amount }) {
      context.commit('updateCartProductAmount', { basketItemId, amount });
      if (amount < 1) {
        return {};
      }
      return axios
        .put('https://vue-moire.skillbox.cc/api/baskets/products', {
          basketItemId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    addProductToItem(context, { basketItemId, amount }) {
      context.commit('addProductToItem', { basketItemId, amount });
      if (amount < 1) {
        return {};
      }
      return axios
        .put('https://vue-moire.skillbox.cc/api/baskets/products', {
          basketItemId,
          quantity: amount + 1,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    remuveProductToItem(context, { basketItemId, amount }) {
      context.commit('remuveProductToItem', { basketItemId, amount });
      if (amount === 1) {
        return {};
      }
      return axios
        .put('https://vue-moire.skillbox.cc/api/baskets/products', {
          basketItemId,
          quantity: amount - 1,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    deleteCartProduct(context, basketItemId) {
      return axios
        .delete('https://vue-moire.skillbox.cc/api/baskets/products', {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
          data: {
            basketItemId,
          },
        })
        .then(() => context.commit('deleteCartProduct', basketItemId))
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
  },
});
