<template>
  <ul class="catalog__pagination pagination">
          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow"
            @click.prevent="paginate(page -1)"
            :class="{'pagination__link--disabled disabled': (page - 1) === 0}"
            aria-label=" ">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </a>
          </li>
          <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
            <a class="pagination__link" @click.prevent="paginate(pageNumber)"
            :class="{'pagination__link--current': pageNumber === page}">
              {{pageNumber}}
            </a>
          </li>
          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow" @click.prevent="paginate(page + 1)"
            :class="{'pagination__link--disabled disabled': page + 1 > pages }" href="#"
            aria-label=" ">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </a>
          </li>
  </ul>
</template>

<style>
  a.disabled {
  pointer-events: none;
}

</style>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page);
    },
  },
};
</script>
