<template>
  <button type="button" aria-label="  " :disabled="item.amount === 1"
  @click="deleteProductItem(item.amount)">
    <svg width="10" height="10" fill="currentColor">
      <use xlink:href="#icon-minus"></use>
    </svg>
  </button>
</template>

<script>
export default {
  props: ['item'],
  methods: {
    deleteProductItem(value) {
      this.$store.dispatch('remuveProductToItem', { basketItemId: this.item.basketItemId, amount: value });
    },
  },
};
</script>
