<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
      <img v-if="product.colors[0].gallery"
      :src="product.colors[0].gallery[0].file.url"
      :alt="product.colors[0].gallery[0].file.name">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
      {{product.title}}
      </a>
    </h3>

    <span class="catalog__price">
      {{product.price | numberFormat}} 
    </span>

    <form class="form" action="#" method="POST" @submit.prevent="addToCart">
      <div class="item__row">
        <ul class="colors colors--black">
        <li class="colors__item" v-for="color in product.colors" :key="color.id">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" v-model="colorId"
            :name="color.color.title" :value="color.color.id" checked="">
            <span class="colors__value" :style="{background: color.color.code}">
            </span>
          </label>
        </li>
      </ul>
        <fieldset class="form__block">
          <legend class="form__legend"></legend>
          <label class="form__label form__label--small form__label--select">
            <select class="form__select" type="text" v-if="productSizeItem"
            name="category" v-model="productSize">
              <option v-for="size in productSizeItem.sizes" :key="size.id" :value="size.id">
                {{size.title}}</option>
            </select>
            <div v-else ></div>
          </label>
        </fieldset>
      </div>
      <button class="item__button button button--primery" type="submit">
                 
      </button>
    </form>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';
import axios from 'axios';

export default {
  data() {
    return {
      productSize: 0,
      productAmount: 1,
      colorId: 0,
      productsData: null,
      productLoadind: false,
      productLoadindFaled: false,
    };
  },
  computed: {
    productSizeItem() {
      return this.productsData;
    },
  },
  filters: { numberFormat },
  props: ['product'],
  methods: {
    ...mapActions(['addProductToCart']),
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
        colorId: this.colorId,
        sizeId: this.productSize,
      })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    loadProducts() {
      this.productLoadind = true;
      this.productLoadindFaled = false;
      axios.get(`https://vue-moire.skillbox.cc/api/products/${this.product.id}`)
        .then((response) => { this.productsData = response.data; })
        .then(() => {
          this.productSize = this.productsData.sizes[0].id
            ? this.productsData.sizes[0].id : 0;
        })
        .then(() => {
          this.colorId = this.productsData.colors[0].color.id
            ? this.productsData.colors[0].color.id : 0;
        })
        .catch(() => { this.productLoadindFaled = true; })
        .then(() => { this.productLoadind = false; });
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
