<template>
  <main class="content container">
    <div class="content__top">

      <div class="content__row">
        <h1 class="content__title">
          
        </h1>
        <span class="content__info">
          {{countProducts}} 
        </span>
        <ButtonChangeProductOnPage v-for="value in productsPerPageUndate" :key="value"
        :value="value" :productsPerPage.sync="productsPerPage"/>
      </div>
    </div>

    <div class="content__catalog">
      <FilterProducts :price-from.sync="filterPriceFrom"
      :priceTo.sync="filterPriceTo" :categoryId.sync="filterCategoryId"
      :seasonsId.sync="filterSeasonsId" :materialId.sync="filterMaterialsId"
      :colorId.sync="filterColorId" />

      <section class="catalog">
        <div v-if="productsLoading"> ...</div>
        <div v-if="productsLoadingFaled">
           
          <button @click.prevent="loadProducts">  </button>
        </div>
        <ProductList :products="products" />
        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage"/>
      </section>
    </div>
  </main>
</template>

<script>
import FilterProducts from '@/components/FilterProducts.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductList from '@/components/ProductList.vue';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import ButtonChangeProductOnPage from '@/components/ButtonChangeProductOnPage.vue';

export default {
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: this.$route.query.filter ? this.$route.query.filter : 0,
      filterColorId: 0,
      filterSeasonsId: [],
      filterMaterialsId: [],
      page: 1,
      productsPerPage: 6,
      productsPerPageUndate: [9, 18, 27],
      materialIds: [],
      seasonIds: [],
      productsData: null,
      productsLoading: false,
      productsLoadingFaled: false,
    };
  },
  filters: { numberFormat },
  computed: {
    products() {
      return this.productsData || [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  components: {
    FilterProducts, BasePagination, ProductList, ButtonChangeProductOnPage,
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFaled = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get('https://vue-moire.skillbox.cc/api/products', {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              seasonIds: this.filterSeasonsId,
              materialIds: this.filterMaterialsId,
              colorIds: this.filterColorId,
            },
          })
          .then((response) => { this.productsData = response.data; })
          .catch(() => { this.productsLoadingFaled = true; })
          .then(() => { this.productsLoading = false; });
      }, 0);
    },
  },
  watch: {
    productsPerPage() {
      this.loadProducts();
    },
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterSeasonsId() {
      this.loadProducts();
    },
    filterMaterialsId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
