<template>
<aside class="filter">
  <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
    <fieldset class="form__block">
      <legend class="form__legend"></legend>
      <label class="form__label form__label--price">
        <input class="form__input" type="text" name="min-price" v-model.number="currentPriceFrom">
        <span class="form__value"></span>
      </label>
      <label class="form__label form__label--price">
        <input class="form__input" type="text" name="max-price" v-model.number="currentPriceTo">
        <span class="form__value"></span>
      </label>
    </fieldset>
    <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <ul class="colors">
          <li class="colors__item" v-for="color in colors" :key="color.id">
            <label class="colors__label">
              <input class="colors__radio sr-only" type="radio"
              name="color" :value="color.id" v-model="currentColorId">
              <span class="colors__value" :style="{ background: color.code }">
              </span>
            </label>
          </li>
        </ul>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend"></legend>
      <label class="form__label form__label--select">
        <select class="form__select" type="text" name="category"
        v-model.number="currentCategoryId">
          <option value="0"> </option>
          <option :value="category.id" v-for="category in categories"
            :key="category.id">{{category.title}}</option>
        </select>
      </label>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend"></legend>
      <ul class="check-list">
        <li class="check-list__item" v-for="material in materials" :key="material.id">
          <label class="check-list__label">
            <input class="check-list__check sr-only"
            type="checkbox" name="material" :value="material.id"
            v-model="currentMaterial">
            <span class="check-list__desc">
              {{material.title}}
              <span>({{material.productsCount}})</span>
            </span>
          </label>
        </li>
      </ul>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend"></legend>
      <ul class="check-list">
        <li class="check-list__item" v-for="season in seasons" :key="season.id">
          <label class="check-list__label">
            <input class="check-list__check sr-only"
            type="checkbox" name="collection" :value="season.id"
            v-model="currentSeason">
            <span class="check-list__desc">
              {{season.title}}
              <span>({{season.productsCount}})</span>
            </span>
          </label>
        </li>
      </ul>
    </fieldset>

    <button class="filter__submit button button--primery" type="submit">
      
    </button>
    <button class="filter__reset button button--second" type="button" @click="reset">
      
    </button>
  </form>
</aside>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: this.$route.query.filter ? this.$route.query.filter : 0,
      categoriesData: null,
      seasonsData: null,
      materialsData: null,
      colorsData: null,
      currentColorId: 0,
      currentSeason: [],
      currentMaterial: [],
    };
  },
  props: ['priceFrom', 'priceTo', 'categoryId', 'seasonsId', 'materialId', 'colorId'],
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    seasons() {
      return this.seasonsData ? this.seasonsData.items : [];
    },
    materials() {
      return this.materialsData ? this.materialsData.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    },
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategoryId = value;
    },
    seasonsId(value) {
      this.currentSeason = value;
    },
    materialId(value) {
      this.currentMaterial = value;
    },
    colorId(value) {
      this.currentColorId = value;
    },
  },
  methods: {
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categoryId', this.currentCategoryId);
      this.$emit('update:seasonsId', this.currentSeason);
      this.$emit('update:materialId', this.currentMaterial);
      this.$emit('update:colorId', this.currentColorId);
    },
    reset() {
      this.$emit('update:priceFrom', 0);
      this.$emit('update:priceTo', 0);
      this.$emit('update:categoryId', 0);
      this.$emit('update:colors', 0);
      this.$emit('update:seasonsId', []);
      this.$emit('update:materialId', []);
      this.$emit('update:colorId', 0);
    },
    loadCategories() {
      axios
        .get('https://vue-moire.skillbox.cc/api/productCategories').then((response) => { this.categoriesData = response.data; });
    },
    loadSeasons() {
      axios
        .get('https://vue-moire.skillbox.cc/api/seasons').then((response) => { this.seasonsData = response.data; });
    },
    loadMaterials() {
      axios
        .get('https://vue-moire.skillbox.cc/api/materials').then((response) => { this.materialsData = response.data; });
    },
    loadColors() {
      axios
        .get('https://vue-moire.skillbox.cc/api/colors').then((response) => { this.colorsData = response.data; });
    },
  },
  created() {
    this.loadCategories();
    this.loadSeasons();
    this.loadMaterials();
    this.loadColors();
  },
};
</script>
