<template>
  <button type="button" aria-label="  "
  @click="addProductToItem(item.amount)">
      <svg width="10" height="10" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
  </button>
</template>

<script>
export default {
  props: ['item'],
  methods: {
    addProductToItem(value) {
      this.$store.dispatch('addProductToItem', { basketItemId: this.item.basketItemId, amount: value });
    },
  },
};
</script>
