<template>
  <router-link class="header__cart" :to="{name: 'cart'}" aria-label="  ">
        <svg width="19" height="24">
          <use xlink:href="#icon-cart"></use>
        </svg>
        <transition  name="bounce">
          <span v-if="!show"
          class="header__count" aria-label=" ">
          {{ $store.state.cartProduct.length }}</span>
        </transition>
  </router-link>
</template>

<script>

export default {
  data() {
    return {
      show: false,
    };
  },
  computed: {
    current() {
      return this.$store.state.cartProduct.length;
    },
  },
  methods: {
    change() {
      this.show = false;
    },
  },
  watch: {
    current() {
      this.show = !this.show;
      setTimeout(this.change, 300);
    },
  },
};
</script>
<style>
.bounce-enter-active {
  animation: bounce-in .5s;
}
@keyframes bounce-in {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}</style>
