<template>
    <ul class="cart__options options">
      <li class="options__item" v-for="item in deliveryType" :key="item.id">
        <label class="options__label">
          <input class="options__radio sr-only" type="radio"
          :name="name" :value="item.id" v-model.number="currentType"
          checked="">
          <span class="options__value">
            {{item.title}} <b>{{item.price}}</b>
          </span>
        </label>
      </li>
    </ul>
</template>

<script>
export default {
  data() {
    return {
      currentType: this.name === 'cash' && this.deliveryType ? this.deliveryType[0].id : 0,
    };
  },
  props: ['deliveryType', 'TypeId', 'name'],
  methods: {
    changeType(value) {
      this.$emit('update:TypeId', value);
    },
  },
  watch: {
    currentType(value) {
      this.changeType(value);
    },
  },
};
</script>
