<template>
<main v-if="!order" class="content container">  </main>
  <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span> {{order.id}}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  .
            &nbsp;    &nbsp; .
               &nbsp;
            &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{order.name}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{order.address}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{order.phone}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{order.email}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{order.paymentType}}
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in order.basket.items" :key="item.id">
              <h3>{{item.product.title}}</h3>
              <b>{{item.price | numberFormat}} </b>
              <span>: {{item.product.id}}</span>
            </li>
          </ul>
          <div class="cart__total">
            <p>: <b>{{order.deliveryType.title}}
              {{order.deliveryType.price  | numberFormat}} </b></p>
            <p> :
              <b>{{(order.totalPrice - order.deliveryType.price) | numberFormat}} </b></p>
            <p>: <b>{{order.basket.items.length}}</b>
               <b>{{order.totalPrice | numberFormat}} </b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

export default {
  data() {
    return {
      orderInfo: null,
    };
  },
  filters: { numberFormat },
  computed: {
    order() {
      return this.orderInfo.orderInfo;
    },
  },
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      this.orderInfo = this.$store.state;
      return;
    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
    this.orderInfo = this.$store.state;
  },
};
</script>
